import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

import Layout from "./Layout";
import Home from "./Home";
import Page1 from "./page1";
import Add from "./add";
import Edit from "./edit";
import PageUsers from "./pageUsers";
import Login from "./Login";
import Register from "./register";
import Account from "./Account";
import Cart from "./Cart";
import Checkout from "./Checkout";
import ProductDetail from "./ProductDetail";
import UserDetail from "./UserDetail"; //  import UserDetail
import ChatBox from "./ChatBox"; //  Thm chat box
import ChatBubble from "./ChatBubble"; //  Bong bng chat

const App: React.FC = () => {
  return (
    <Router>
      {/* Bong bng chat lun hin th trn mi trang */}
      <ChatBubble />

      <Routes>
        {/* Layout chnh */}
        <Route path="/" element={<Layout />}>
          {/* Trang mc nh */}
          <Route index element={<Home />} />

          {/* Danh sch sn phm */}
          <Route path="page1" element={<Page1 />} />

          {/* Chi tit sn phm */}
          <Route path="product/:id" element={<ProductDetail />} />

          {/* Thm sn phm */}
          <Route path="add" element={<Add />} />

          {/* Chnh sa sn phm */}
          <Route path="edit/:id" element={<Edit />} />

          {/* Qun l ngi dng */}
          <Route path="users" element={<PageUsers />} />
          {/* Chi tit user */}
          <Route path="users/:id" element={<UserDetail />} />

          {/* Gi hng */}
          <Route path="cart" element={<Cart />} />
          <Route path="checkout" element={<Checkout />} />

          {/* Auth */}
          <Route path="login" element={<Login />} />
          <Route path="register" element={<Register />} />

          {/* Trang ti khon */}
          <Route path="account" element={<Account />} />

          {/* Chat ring  nu mun m ton mn hnh */}
          <Route path="chat" element={<ChatBox />} />
        </Route>

        {/* Fallback 404 */}
        <Route
          path="*"
          element={
            <h2 style={{ textAlign: "center", marginTop: "50px" }}>
              Trang khng tn ti
            </h2>
          }
        />
      </Routes>
    </Router>
  );
};

export default App;
