import React, { useEffect, useState } from "react";
import { Outlet, Link, useNavigate, useLocation } from "react-router-dom";

import anhlogo from "./assets/images/logo.jpg";
import bannerImg from "./assets/images/banner.jpg";

import img1 from "./assets/images/1.jpg";
import img2 from "./assets/images/2.jpg";
import img3 from "./assets/images/3.jpg";
import img4 from "./assets/images/4.jpg";
import img5 from "./assets/images/5.jpg";

import "./assets/css/main.css";

const slideImages = [img1, img2, img3, img4, img5];

const Layout = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const location = useLocation();
  const navigate = useNavigate();
  const [user, setUser] = useState<any>(null);
  const [cartCount, setCartCount] = useState(0);

  useEffect(() => {
    const loadUser = () => {
      const storedUser = localStorage.getItem("user");
      setUser(storedUser ? JSON.parse(storedUser) : null);
    };

    loadUser(); // chy ngay ln u

    window.addEventListener("storage", loadUser);
    window.addEventListener("userUpdated", loadUser);

    return () => {
      window.removeEventListener("storage", loadUser);
      window.removeEventListener("userUpdated", loadUser);
    };
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("user");

    //  BN TN HIU CHO APP BIT  LOGOUT
    window.dispatchEvent(new Event("userUpdated"));

    navigate("/login");
  };

  useEffect(() => {
    const interval = setInterval(() => nextSlide(), 4000);
    return () => clearInterval(interval);
  }, []);

  const nextSlide = () =>
    setCurrentSlide((prev) => (prev + 1) % slideImages.length);
  const prevSlide = () =>
    setCurrentSlide((prev) => (prev === 0 ? slideImages.length - 1 : prev - 1));

  const updateCartCount = () => {
    const cart = JSON.parse(localStorage.getItem("cart") || "[]");
    const total = cart.reduce(
      (sum: number, item: any) => sum + item.quantity,
      0
    );
    setCartCount(total);
  };

  useEffect(() => {
    updateCartCount();
    window.addEventListener("cartUpdated", updateCartCount);
    window.addEventListener("storage", updateCartCount);
    return () => {
      window.removeEventListener("cartUpdated", updateCartCount);
      window.removeEventListener("storage", updateCartCount);
    };
  }, []);

  return (
    <div
      style={{
        margin: 0,
        fontFamily: "Arial, sans-serif",
        background: "black",
      }}
    >
      {" "}
      <div id="head">
        {/* HEADER */}
        <div
          id="banner"
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            padding: "10px 20px",
            height: "140px",
            backgroundColor: "#111",
          }}
        >
          <img
            src={anhlogo}
            alt="Logo"
            style={{ width: "160px", height: "auto", objectFit: "contain" }}
          />

          <div
            style={{
              flex: 1,
              margin: "0 20px",
              borderRadius: "10px",
              overflow: "hidden",
            }}
          >
            <img
              src={bannerImg}
              style={{ width: "100%", height: "100%", objectFit: "cover" }}
            />
          </div>

          <div style={{ width: "250px" }}>
            <input
              type="text"
              placeholder="Tm kim..."
              style={{
                width: "100%",
                padding: "8px",
                borderRadius: "4px",
                border: "1px solid #ccc",
              }}
            />
          </div>
        </div>

        {/* MENU */}
        <div
          style={{
            height: "70px",
            backgroundColor: "black",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            position: "relative",
          }}
        >
          <ul
            style={{
              listStyle: "none",
              display: "flex",
              alignItems: "center",
              margin: 0,
              padding: 0,
            }}
          >
            {[
              { to: "/", label: "Trang Ch" },
              { to: "/page1", label: "Shop" },
              { to: "/users", label: "Thnh Vin" },
              { to: "#", label: "Gii Thiu" },
            ].map((item, i) => (
              <li key={i} style={{ margin: "0 20px" }}>
                <Link
                  to={item.to}
                  style={{
                    color: "white",
                    textDecoration: "none",
                    fontWeight: "bold",
                    fontSize: "16px",
                  }}
                >
                  {item.label}
                </Link>
              </li>
            ))}
          </ul>

          {/* GI HNG */}
          <Link
            to="/cart"
            style={{
              color: "white",
              marginLeft: 20,
              fontSize: 18,
              position: "relative",
              textDecoration: "none",
              fontWeight: "bold",
            }}
          >
             Gi hng
            {cartCount > 0 && (
              <span
                style={{
                  position: "absolute",
                  top: "-10px",
                  right: "-15px",
                  background: "red",
                  color: "white",
                  borderRadius: "50%",
                  padding: "2px 7px",
                  fontSize: "12px",
                  fontWeight: "bold",
                }}
              >
                {cartCount}
              </span>
            )}
          </Link>

          {/* NT NG NHP / NG XUT TCH RING */}
          {!user && (
            <Link
              to="/login"
              style={{
                position: "absolute",
                right: "20px",
                background: "green",
                color: "white",
                padding: "10px 15px",
                borderRadius: "6px",
                textDecoration: "none",
                fontWeight: "bold",
              }}
            >
              ng nhp
            </Link>
          )}

          {user && (
            <button
              onClick={handleLogout}
              style={{
                position: "absolute",
                right: "20px",
                background: "red",
                color: "white",
                padding: "10px 15px",
                border: "none",
                borderRadius: "6px",
                cursor: "pointer",
                fontWeight: "bold",
              }}
            >
              ng xut
            </button>
          )}
        </div>
      </div>
      {/* SLIDE */}
      {location.pathname === "/" && (
        <div
          style={{
            width: "100%",
            height: "500px",
            overflow: "hidden",
            position: "relative",
          }}
        >
          {slideImages.map((src, index) => (
            <img
              key={index}
              src={src}
              style={{
                width: "100%",
                height: "100%",
                objectFit: "cover",
                position: "absolute",
                opacity: currentSlide === index ? 1 : 0,
                transition: "1s",
              }}
            />
          ))}
        </div>
      )}
      <div id="container" className="container">
        <Outlet />
      </div>
      {/* FOOTER */}
      {/* FOOTER */}
      <div
        style={{
          marginTop: "60px",
          backgroundColor: "#111",
          color: "white",
          padding: "60px 40px",
        }}
      >
        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            gap: "40px",
            alignItems: "flex-start",
            justifyContent: "space-between",
            maxWidth: "1300px",
            margin: "0 auto",
          }}
        >
          {/* LEFT - LOGO + DESCRIPTION */}
          <div style={{ flex: "1 1 350px" }}>
            <img
              src={anhlogo}
              alt="Logo Footer"
              style={{
                width: "200px",
                height: "auto",
                objectFit: "contain",
                marginBottom: "15px",
              }}
            />
            <p style={{ fontSize: "15px", color: "#ccc", lineHeight: "1.6" }}>
              Cng ty chng ti cung cp sn phm cht lng cao cng dch v h
              tr tn tm. Rt hn hnh c phc v qu khch!
            </p>
          </div>

          {/* RIGHT - GOOGLE MAP */}
          <div style={{ flex: "1 1 500px" }}>
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6591.547227653123!2d106.70215355124742!3d10.78391243881112!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31752f4b3330bcc7%3A0x4db964d76bf6e18e!2sSaigon%20Zoo%20%26%20Botanical%20Gardens!5e0!3m2!1sen!2s!4v1765006329678!5m2!1sen!2s"
              width="100%"
              height="320"
              style={{
                border: "0",
                borderRadius: "12px",
                width: "100%",
                boxShadow: "0 0 15px rgba(255,255,255,0.1)",
              }}
              allowFullScreen
              loading="lazy"
              referrerPolicy="no-referrer-when-downgrade"
            ></iframe>
          </div>
        </div>

        {/* COPYRIGHT */}
        <div
          style={{
            textAlign: "center",
            marginTop: "40px",
            paddingTop: "20px",
            borderTop: "1px solid #333",
            fontSize: "14px",
            color: "#888",
          }}
        >
           2025 Your Company. All rights reserved.
        </div>
      </div>
    </div>
  );
};

export default Layout;
