// src/edit.tsx
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { supabase } from "./supabaseClient";

const Edit: React.FC = () => {
  const { id } = useParams<{ id: string }>();

  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState<number>(0);
  const [image, setImage] = useState("");
  const [stock, setStock] = useState<number>(0); //  THM TN KHO
  const [message, setMessage] = useState("");

  useEffect(() => {
    if (id) fetchProduct();
  }, [id]);

  const fetchProduct = async () => {
    const { data, error } = await supabase
      .from("products")
      .select("*")
      .eq("id", id)
      .single();

    if (error) console.error(error.message);
    else {
      setName(data.name);
      setDescription(data.description);
      setPrice(data.price);
      setImage(data.image);
      setStock(data.stock); //  LY TN KHO
    }
  };

  const handleUpdate = async (e: React.FormEvent) => {
    e.preventDefault();
    const { error } = await supabase
      .from("products")
      .update({
        name,
        description,
        price,
        image,
        stock, //  CP NHT TN KHO
      })
      .eq("id", id);

    if (error) setMessage(" Li: " + error.message);
    else setMessage(" Cp nht thnh cng!");
  };

  return (
    <div style={{ padding: "20px", maxWidth: "500px", margin: "0 auto" }}>
      <h2>Chnh sa sn phm</h2>
      <form onSubmit={handleUpdate}>
        <div>
          <label>Tn sn phm:</label>
          <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
            style={{ width: "100%", marginBottom: "10px" }}
          />
        </div>

        <div>
          <label>M t:</label>
          <textarea
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            required
            style={{ width: "100%", marginBottom: "10px" }}
          />
        </div>

        <div>
          <label>Gi (VN):</label>
          <input
            type="number"
            value={price}
            onChange={(e) => setPrice(Number(e.target.value))}
            required
            style={{ width: "100%", marginBottom: "10px" }}
          />
        </div>

        <div>
          <label>Link hnh nh:</label>
          <input
            type="text"
            value={image}
            onChange={(e) => setImage(e.target.value)}
            style={{ width: "100%", marginBottom: "10px" }}
          />
        </div>

        {/*  INPUT TN KHO */}
        <div>
          <label>Tn kho:</label>
          <input
            type="number"
            value={stock}
            onChange={(e) => setStock(Number(e.target.value))}
            required
            style={{ width: "100%", marginBottom: "10px" }}
          />
        </div>

        <button type="submit">Lu thay i</button>
      </form>

      {message && <p>{message}</p>}
    </div>
  );
};

export default Edit;
